import React, { useState, useRef, useEffect } from 'react';
import GoogleMapReact from 'google-map-react';
import SelectedMarkers from './SelectedMarkers';
import axios from "axios";
function CustomGoogleMap(props) {
  const [mapMarkers, setMapMarkers] = useState([]);
  const mapMarkersRef = useRef(mapMarkers);
  const previousMarkersRef = useRef([]);
  const mapRef = useRef();
  const drawingManagerRef = useRef();
  const [currentPolygon, setCurrentPolygon] = useState(null)
  const [data, setData] = useState(null)
  const [dataWeather, setDataWeather] = useState(null)
  function setMapMarkersRef(data) {
    // mapMarkersRef.current = data;
    //  setMapMarkers(data);
  }

  async function postData(url = '', data ) {
    let dataC = JSON.stringify(data)
  //  alert(dataC)
    const options = {
      method: 'GET',
      url: 'https://api.ambeedata.com/latest/by-lat-lng',
      params: JSON.parse(dataC),
      headers: {'x-api-key': '73adc3f9bac7b975765041223b6c3b6f7c23e2bb7c09ca7dfef3a8728ed69c7f', 'Content-type': 'application/json'}
    };
    axios.request(options).then(function (response) {
    // alert(JSON.stringify(response))
      setData(response.data);
    }).catch(function (error) {
      console.error(error);
    });
  }
  async function getData(url = '', data ) {
    let dataC = JSON.stringify(data)
  //  alert(dataC)
    const options = {
      method: 'GET',
      url: 'https://api.ambeedata.com/weather/latest/by-lat-lng',
      params: JSON.parse(dataC),
      headers: {'x-api-key': '73adc3f9bac7b975765041223b6c3b6f7c23e2bb7c09ca7dfef3a8728ed69c7f', 'Content-type': 'application/json'}
    };
    axios.request(options).then(function (response) {
    // alert(JSON.stringify(response))
    setDataWeather(response.data);
    }).catch(function (error) {
      console.error(error);
    });
  }



useEffect(()=>{

  props.getInfo(data);
  //alert(JSON.stringify(props))

},[data])
useEffect(()=>{

  props.getWeather(dataWeather);
},[dataWeather])


  function _initDefaultMarkers(google, coord) {
    var myLatLng = { lat: coord.lat, lng: coord.lng };
    const marker = new google.maps.Marker({
      position: myLatLng,
      title: "Marker 1"
    });
    // marker.addListener('click', _onMarkerClick)
    //setMapMarkersRef([marker]);
    marker.setMap(mapRef.current);
  }

  function _onMarkerClick(marker) {
    //  alert(`lat: ${marker.latLng.lat()} - long: $${marker.latLng.lng()}`)
  }

  function _onPolygonClick(google) {
    return function (polygon) {
      // Remove polygon on click
      this.setMap(null);

      // Show all markers on left side panel again after removing polygon
      setMapMarkersRef(previousMarkersRef.current)
      // Show all markers on map again after removing polygon
      previousMarkersRef.current.forEach(marker => {
        //  marker.setMap(mapRef.current)
      });

      // Revert drawing mode
      drawingManagerRef.current.setOptions({
        drawingControlOptions: {
          position: google.maps.ControlPosition.TOP_CENTER,
          drawingModes: ['polygon']
        }
      });
    }
  }

  function _onMarkerComplete(marker) {

    marker.setTitle(`Marker ${mapMarkersRef.current.length + 1}`);
    marker.addListener('click', _onMarkerClick)
    const newMarkers = [...mapMarkersRef.current, marker];
    console.log((newMarkers))
    setMapMarkersRef(newMarkers);
  }
  function getPaths(polygon) {

    var polygonBounds = polygon.getPath();
    var bounds = [];
    for (var i = 0; i < polygonBounds.length; i++) {
      var point = {
        lat: polygonBounds.getAt(i).lat(),
        lng: polygonBounds.getAt(i).lng()
      };
      bounds.push(point);
    }
    return bounds
  }


  function _onPolygonComplete(google) {
    return async function (polygon) {
      setCurrentPolygon(polygon)
      var polygonCoords = []
      var bounds = new google.maps.LatLngBounds();
      let coords = (getPaths(polygon))
      console.log(coords)
      coords.map((data) => {
        polygonCoords.push(new google.maps.LatLng(data.lat, data.lng),)
      })

      for (let i = 0; i < polygonCoords.length; i++) {
        bounds.extend(polygonCoords[i]);
      }
      let center = bounds.getCenter();
      console.log(JSON.stringify(center.coor))
   
      var myLatLng = center;
      const marker = new google.maps.Marker({
        position: myLatLng,
        title: "Marker 1"
      });
      marker.setMap(mapRef.current);
     polygon.addListener('click', _onPolygonClick(google));
      //  previousMarkersRef.current = mapMarkersRef.current;
  
      drawingManagerRef.current.setOptions({
        drawingControlOptions: {
          position: google.maps.ControlPosition.TOP_CENTER,
          drawingModes:  ['polygon']
        }
      });
      drawingManagerRef.current.setDrawingMode(null);

     await postData('https://api.ambeedata.com/latest/by-lat-lng', myLatLng)
     await getData('https://api.ambeedata.com/latest/by-lat-lng', myLatLng)
     
  .then(data => {
   // alert(JSON.stringify(data)); // JSON data parsed by `data.json()` call
  })
     //   alert( JSON.stringify( postData(,JSON.stringify(myLatLng))))

    }
    
  }

  function _createGoogleMapDrawingManager(google) {
    const drawingManager = new google.maps.drawing.DrawingManager({
      //drawingMode: google.maps.drawing.OverlayType.MARKER,
      drawingControl: true,
      drawingControlOptions: {
        position: google.maps.ControlPosition.TOP_CENTER,
        drawingModes: ['polygon']
      },
      polygonOptions: {
        fillColor: '#000000',
        fillColor: 'green',
        fillOpacity: 0.5,
        strokeWeight: 3,
        editable: true,
        zIndex: 1
      }
    });

    return drawingManager;
  }

  function _onGoogleApiLoaded(google) {
    const map = google.map
    mapRef.current = map;
    const drawingManager = _createGoogleMapDrawingManager(google);
    drawingManagerRef.current = drawingManager;

    //  google.maps.event.addListener(drawingManager, 'markercomplete', _onMarkerComplete);
    google.maps.event.addListener(drawingManager, 'polygoncomplete', _onPolygonComplete(google));
    drawingManager.setMap(map);

    // _initDefaultMarkers(google)
  }


  return (
    <div style={{ width: '100%', margin: '0 auto' }}>
      <div className="columns">
        <div className="column" style={{ height:props.height }}>
          
          <GoogleMapReact

            bootstrapURLKeys={{ key: 'AIzaSyAChufWiMfwsmyX3Se1dRaN4t31z0xmIMo', libraries: ['drawing', 'geometry'].join(','), }}
            defaultCenter={props.center}
            defaultZoom={props.zoom}
            yesIWantToUseGoogleMapApiInternals
            onGoogleApiLoaded={_onGoogleApiLoaded} />
        </div>
       
      </div>
    </div>
  )
}

CustomGoogleMap.defaultProps = {
height:500,
  center: {
    lat: 40.69276097550692,
    lng: -4.7392
  },
  zoom: 5
}

export default CustomGoogleMap;